import React, {Component} from 'react'
import './App.css';
import axios from './axios-users'
import Cardlist from './components/Cardlists/Cardlists'
import Searchbox from './components/Searchbox/Searchbox'


class App extends Component{

  constructor() {

      super();

      this.state = {

          monsters : [],

          searchField : ''

      }

    }


    changeNameHandler = () => {

        this.setState({string : 'Hello Dude!'})

    }

    searchFieldHandler = (event) =>{

        this.setState({searchField : event.target.value})

    }

    //Using AXIOS to fetch data from API --

    componentDidMount() {

        axios.get('/users')

            .then((response) => {

                this.setState({monsters:response.data})
            })

            .catch((error)=> {

                console.log('An error occured')
            })
    }
 
    render(){

        const {monsters, searchField}  = this.state

        const filteredMonsters = monsters.filter((monster) => {

            return monster.name.toLowerCase().includes(searchField.toLowerCase())

        })

        return (

            <div className="App">

                <h1>Monster Rolodex</h1>

                <Searchbox placeholder = 'Search monsters' searchFieldHandler = {this.searchFieldHandler} />

                <Cardlist monsters = {filteredMonsters}/>

            </div>

        )
    }ssss

    
}


export default App;
